@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
@import url(https://fonts.googleapis.com/css?family=PT+Sans|Roboto+Slab);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-item {
    width: 186px;
    padding: 0 2px;
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
    overflow: hidden;
    font-size: 15px;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
}
.menu-item span:hover {
    text-decoration: underline;
}
.menu-item-wrapper {
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
}

.menu-item-wrapper span {
    position: absolute;
    padding: 4px 8px;
}

nav .menu-item svg {
    padding: 6px;
    width: 20px !important;
    overflow: hidden;
}
nav .menu-item .sub-menu .menu-item {
    font-size: 13px;
}
.sub-menu {
    margin: 0 -2px 0 -2px;
    max-height: 0;
    overflow: hidden;
    -webkit-transition: max-height 0.3s ease-in-out;
    transition: max-height 0.3s ease-in-out;
}
.sub-menu-indicator, .sub-menu-indicator svg {
    position: absolute;
    -webkit-transition: 0.26s ease-in-out;
    transition: 0.26s ease-in-out;
    right: 0;
}
.sub-menu-indicator svg[data-icon='box-open'] {
    opacity: 0;
}
.menu-item:hover > .sub-menu-indicator svg[data-icon='box'],
.menu-item.active > .sub-menu-indicator svg[data-icon='box'],
.menu-item.sub-active > .sub-menu-indicator svg[data-icon='box']
{
    opacity: 0;
}
.menu-item:hover > .sub-menu-indicator svg[data-icon='box-open'],
.menu-item.active > .sub-menu-indicator svg[data-icon='box-open'],
.menu-item.sub-active > .sub-menu-indicator svg[data-icon='box-open']
{
    opacity: 1;
}
.menu-item:hover > .sub-menu,
.active > .sub-menu,
.sub-active > .sub-menu
{
    max-height: 500px;
}
.sub-active, .sub-menu {
    color: rgb(30, 30, 30) !important;
    background: rgb(235, 235, 235);
}
.active {
    color: rgb(20, 20, 20) !important;
    background: rgb(205, 205, 205);
}

.icon {
    color: white;
    cursor: pointer;
    font-size: 24px;
}
.icon div {
    font-style: normal;
    position: absolute;
}
.icon .one {
    margin-left: 20px;
    font-size: 12px;
}
.icon .two {
    color: #82d2d2;
    margin: 3px 0 0 75px;
}
.icon .three {
    color: #82d2d2;
    margin: 12px 0 0 20px;
    font-size: 12px;
}
.icon .vh {
    -webkit-transform: scale(-1, -1);
    transform: scale(-1, -1);
}
item {
    cursor: pointer;
    float: left;
    width: 100%;
}
.fa-interface {
   border: 1px solid red;
   float: right;
   width: 100%;
   height: 40px;
   display: flex;
   justify-content: right;
}
.fa-interface svg {
    padding: 10px;
    cursor: pointer;
    font-size: 24px;
    -webkit-transition: 0.15s linear;
    transition: 0.15s linear;
}
.fa-interface svg:hover {
    background: rgba(255, 255, 255, 0.2);
}

svg[data-icon="caret-left"].h {
}
svg[data-icon="caret-left"] {
}
.settings {
    float: left;
    width: 100%;
}
.settings .fas {
    color: white;
    cursor: pointer;
    opacity: 0.8;
    position: absolute;
}
.settings .light {
    font-size: 18px;
    margin: -8px 0 0 17px;
    padding: 13px;
}

nav {
    background: -webkit-linear-gradient(left, #162618 0%, #162618 71%, #0e2310 99%);
    background: #162618;
    background: linear-gradient(to right, #162618 0%, #162618 71%, #0e2310 99%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='@color0', endColorstr='@color2', GradientType=1);
    font-family: 'Montserrat', sans-serif;
    color: white;
    height: 100vh;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 190px;
    -webkit-transition: width 0.2s ease-in-out;
    transition: width 0.2s ease-in-out;
}
nav.hidden {
    width: 0;
}
nav.small {
    width: 40px;
}
nav.small > .menu-item .sub-menu-indicator {
    opacity: 0.0;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'PT Sans', sans-serif;
}
p {
    font-family: 'Roboto Slab', serif;
}
.content {
    font-family: 'Crete Round', serif;
    padding-left: 190px;
    width: calc(100% - 190px);
    min-height: 100vh;
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    line-height: 1.5em;
    overflow: hidden;
}
.section {
    float: left;
    clear: both;
    padding: 15px;
}
.footer {
    width: 100%;
    padding: 10px;
}
.footer p {
    color: rgb(70, 70, 70);
    font-size: 12px;
    text-align: center;
    width: 100%;
}
nav.small ~ .content {
    width: calc(100% - 40px);
    padding-left: 40px;
}

@keyframes animatedBackground {
  from {
    background-position: 0px 0px; }
  to {
    background-position: 640px 400px; }
}

@-webkit-keyframes animatedBackground {
  from {
    background-position: 0px 0px; }
  to {
    background-position: 640px 400px; }
}

@keyframes animatedBackground_bg {
  from {
    background-position: 0px 0px; }
  to {
    background-position: -640px -800px; }
}

@-webkit-keyframes animatedBackground_bg {
  from {
    background-position: 0px 0px; }
  to {
    background-position: -640px 400px; }
}

.section-image-wrapper,
.section.section-large {
    min-height: 400px;
    width: 100%;
    background-repeat: repeat;
}

.section[data-background='star'] {
    color: rgb(230, 230, 230) !important;
    background-image: url(data:image/gif;base64,);
    background-color: rgb(15, 15, 15);
    -webkit-animation: animatedBackground_bg 130s linear infinite;
    animation: animatedBackground_bg 130s linear infinite;
}

.section[data-background='star'] .section-image-wrapper {
    background-image: url(data:image/gif;base64,);
    -webkit-animation: animatedBackground 90s linear infinite;
    animation: animatedBackground 90s linear infinite;
}

.section[data-background='star'] .section-links {
    color: rgb(230, 230, 230);
}

.section[data-background='star'] p,
.section[data-background='star'] h1,
.section[data-background='star'] img
{
    background: rgba(35, 35, 35, 0.9);
    color: rgb(230, 230, 230);
}
.section[data-background='star'] .section-links a {
    color: rgb(230, 230, 230);
}

.section-image-wrapper p,
.section-image-wrapper h1,
.section-left-header h1,
.section-right img {
    padding: 0.5em 0.8em;
    margin: 0.6em;
}
.section-lr {
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
}
.section-left, .section-right {
    width: 45%;
    min-width: 400px;
}
.section-left-header {
    width: 100%;
}
.section-links {
    float: left;
    font-size: 24px;
    margin-top: 1em;
}
.section-left-header .section-links a {
    margin: 1em;
    color: rgb(35, 35, 35);
}
.section-right {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.section-right img {
    width: calc(100% - 1.2em);
}

@media only screen and (max-width: 960px) {
  .section-lr {
    justify-content: center;
  }
}

.section
.section-content,
.footer,
p,
h1,
h2 {
    clear: both;
    float: left;
    padding: 0.1em 0.8em;
}
p {
    max-width: 700px;
}
h1 {
    font-size: 24px;
}
h2 {
    font-size: 18px;
    padding-left: 2em;
}
a, a:visited {
    color: rgb(50, 50, 200);
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
}
body {
    background: -webkit-linear-gradient(left, #d7dbaf 0%, #f9f5d1 42%, #f9f5d1 100%);
    background: linear-gradient(to right, #d7dbaf 0%, #f9f5d1 42%, #f9f5d1 100%);
    background: #fff0eb;
}
.fas:hover {
    background: rgba(255, 255, 255, 0.2);
    opacity: 1;
}
.h {
    -webkit-transform: scale(-1, 1);
    transform: scale(-1, 1);
}
br {
    margin-bottom: 15px;
}

